import React from 'react';
import './PlayerViewCover.css';


class PlayerViewCover extends React.Component {
	constructor( props ) {
		super( props );
		this.className = 'c-player__preloader';
	}

	componentDidMount() {
		this.props.togglePreloader( false );
	}

	componentDidUpdate() {
		this.props.togglePreloader( this.props.active );

		if ( this.props.activePreloader ) {
			this.className = 'c-player__preloader c-player-preloader--active';
		} else {
			this.className = 'c-player-preloader';
		}
	}
	
	render() {
		return (
			<div id="cover" className="c-player__cover" style={{background: 'url( \'' + this.props.cover + '\' )'}}>
				<div className={this.className} />
			</div>
		);
	}
};


export default PlayerViewCover;
