import React from 'react';
import { Panel } from '@vkontakte/vkui';
import { Header, Content, Category, CategoryList, Input, H, Subtitle, Center, CenterLogo, SecondaryButton } from '../components/index.js';


class NotFound extends React.Component {
	constructor( props ) {
		super( props );	
	}

	main = () => {
		this.props.navigate( '/main' );
	}

	render() {
		return (
			<Panel id={this.props.id}>
				<Header icon="browser_back" onClick={this.main}>  </Header>
				<Content>
					<br />
				
					<Center>
						<CenterLogo src="/images/sad.png" />
						<H level="3">  </H>
						<Subtitle> .</Subtitle>
						<p> ,   .      .</p>

						<SecondaryButton onClick={this.main}> </SecondaryButton>
					</Center>
					<br />
				</Content>
			</Panel>
		);
	}
};


export default NotFound;
